.iframe {
   width: 1000px;
   height: 500px;
   margin: 30px auto 30px;
}

@media screen and (max-width: 1100px) {
   .iframe {
      width: 850px;
      height: 400px;
   }
}

@media screen and (max-width: 880px) {
   .iframe {
      width: 800px;
      height: 400px;
   }
}

@media screen and (max-width: 800px) {
   .iframe {
      width: 600px;
      height: 400px;
   }
}


