.about {
  margin: 60px 100px;
}

h3 {
  font-size: 25px;
}
section {
  display: -ms-flexbox;
  display: flex;
}
.about-sections {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  margin: 0 auto;
  padding: 0 30px;
  width: 300px;
}
img {
  width: 100px;
}
.icon {
  margin: 0 auto;
}
@-webkit-keyframes icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.icon:hover {
  -webkit-animation: icon-spin infinite 400ms linear;
          animation: icon-spin infinite 400ms linear;
}

@media screen and (max-width: 1100px) {
  .about {
    margin: 45px 90px;
  }
  h1 {
    margin-bottom: 30px;
    font-size: 35px;
  }
  img {
    width: 85px;
  }
  h3 {
    font-size: 20px;
  }
  p {
    margin: 0;
    font-size: 14px;
  }
}

@media screen and (max-width: 840px) {
  .about {
    margin: 35px 20px;
  }
  h1 {
    font-size: 35px;
  }
  img {
    width: 60px;
  }
  h3 {
    font-size: 20px;
  }
  p {
    margin: 0;
    font-size: 14px;
  }
  .about-sections {
    padding: 0 20px;
    width: 300px;
  }
}

@media screen and (max-width: 600px) {
  .about {
    margin: 35px 20px;
  }
  h1 {
    font-size: 35px;
  }
  img {
    width: 80px;
  }
  .about-sections {
    margin-bottom: 50px;
  }
  section {
    display: block;
  }
}


