.portfolio {
  margin: 60px 100px;
}
section {
  margin: 0 auto;
  text-align: center;
}

.proj-description {
  width: 400px;
}
.gif {
  width: 400px;
  border: 2px solid #9c3f5f;
  padding: 3px;
}

.line {
  height: 2px;
  background: -webkit-gradient(
    radial,
    50% 50%,
    0,
    50% 50%,
    200,
    from(#9c3f5f),
    to(white)
  );
}
.line-bottom {
  margin-top: -4px;
  padding-top: 2px;
}
.project-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

@media screen and (max-width: 630px) {
  .portfolio {
    margin: 0 auto;
  }
}


